    /*弹出框显示时，禁止底层滑动*/
    Vue.prototype.setSlide = function (option) {
      var body = document.body,
          scrollTop = body.scrollTop;
      if (option === 0) {
        body.className = 'body-fixed';
        body.style.top = scrollTop * -1 + 'px';
      } else {
        body.className = '';
        body.scrollTop = body.style.top.slice(0, -2) * -1;
        body.style.top = null;
      }
    };

